import Vuex from "vuex"
import Vue from "vue"
import axios from "axios"
import createPersistedState from "vuex-persistedstate";



Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        products: [],
        featured_products: [],
        last_products: []
    },
    plugins: [createPersistedState()],
    mutations: {
        SET_PRODUCTS_TO_STATE: (state, response) => {
            state.products = response;
        },
        SORT_FEATURED_PRODUCTS:(state) => {
            let prod_array = [];
            for (const [key, value] of Object.entries(state.products)){
                prod_array.push({
                    id: key,
                    ... value
                })
            }
           state.featured_products = prod_array.sort(function(a, b){
                return a.rating - b.rating;
            });
        },
        SORT_LAST_PRODUCTS:(state) => {
            let last_prod_array = [];
            for (const [key, value] of Object.entries(state.products)){
                last_prod_array.push({
                    id: key,
                    ... value
                })
            }
            state.last_products = last_prod_array.sort(function(a, b){
                return a.age - b.age;
            });
        },
    }, 
    actions: {    
        GET_PRODUCTS({commit}){
            return axios.get("https://raw.githubusercontent.com/xSANCHOSx/tzgrodas/gh-pages/data.json")
                .then((response) => {
                    commit('SET_PRODUCTS_TO_STATE', response.data);
                    return response;
                })
                .catch((error) => {
                    console.log(error);
                    return error;
                })
        },
        GET_FEATURED({commit}){
            commit('SORT_FEATURED_PRODUCTS');
        },
        GET_LAST({commit}){
            commit('SORT_LAST_PRODUCTS');
        }
    },
    getters: {
        PRODUCTS (state) {
            return state.products;
        },
        FEATURED (state) {
            return state.featured_products;
        },
        LAST (state) {
            return state.last_products;
        }
    }
});

export default store